.title {
    color: #9E41C6;
    margin: 0;
    font-size: 20px;
    display: flex;
    gap: 10px;
    .titleText{
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-image: linear-gradient(
                        45deg,
                        #9E41C6 25%,
                        white 100%
        );
    }
}
hr {
    opacity: 20%;
}
.textBLock {
    color: white;
    padding: 5px;
    position: relative;
    display: inline-block;
    vertical-align: top;
    font-size: 16px;
    text-decoration: none;
    .textEducation {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
        .textCoreSkills {
            gap: 20px;
        }
        p {
            margin: 5px;
        }
    }
}